.navbar__menu {
  display: flex;
  font-size: 2rem;
  justify-content: center;
  align-items: center;
}
.navbar__menu__item {
  position: relative;
  padding: 0.8rem 1.2rem;
  margin: 0 0.8rem;
  cursor: pointer;
  transition: all 1s ease-out;
  list-style: none;
  color: #fff;
}
.navbar__menu__item::after {
  content: "";
  display: block;
  position: absolute;
  width: 0%;
  height: 2px;
  left: 0;
  margin-top: 4px;
  transition: all 500ms ease-out;
}
.navbar__menu__item:hover {
  color: #ff4200;
  color: var(--color-red-color);
}
.navbar__menu__item > .active {
  color: #ff4200;
  color: var(--color-red-color);
}
.navbar__menu__item > .active::after,
.navbar__menu__item:hover:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  margin-top: 4px;
  background: #ff4200;
  background: var(--color-red-color);
}
.navbar__toggle {
  display: none;
}
